.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;



  &__logo-link {
    @media (max-width: 768px) {

      text-align: center;
      width: 100%;
    }
  }

  // &__logo {}


  .main-nav__button {
    position: absolute;
    top: 30px;
    right: 20px;
    display: none;
    width: 32px;
    height: 22px;
    padding: 0;
    border: none;
    background-color: transparent;
    background-image: url(../img/icon-menu.svg);
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 1;

    &--active {
      width: 20px;
      height: 20px;
      background-image: url(../img/icon-close.svg) !important;

    }

    @media (max-width: 768px) {
      display: block;
    }
  }

  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;

    @media (max-width: 768px) {
      position: absolute;
      left: 0;
      top: 0;
      display: none;
      width: 100%;
      padding-top: 30px;
      padding-left: 20px;
      background-color: #181818;

      &--opened {
        display: block;
      }
    }
  }

  &__item {
    margin-right: 60px;

    @media (max-width: 768px) {
      margin-bottom: 20px;
    }
  }

  &__link {
    text-transform: uppercase;
    font-size: 18px;
    line-height: 21px;
    color: $white;
    transition: 100ms;
    opacity: 1;

    &--current {
      opacity: 0.6;
    }

    &:not([href]) {
      opacity: 0.6;
    }

    &:hover {
      opacity: 0.6;
      transition: 100ms;
    }

    @media (max-width: 768px) {
      font-size: 20px;
      opacity: 0.6;
    }
  }

  a:not([href]) {
    cursor: default;
  }
}

.page-header {
  .main-nav__button {
    background-image: url('../img/icon-menu.svg');
  }
}

.page-header--gallery {
  .main-nav__button {
    background-image: url('../img/icon-menu-black.svg');
  }
}
