.page-header {
  position: fixed;
  z-index: 10;
  margin: 0 auto;
  width: 100%;

  &--gallery,
  &--about {
    position: static;
    margin-bottom: 70px;
    color: #000000;

    @media (max-width: 768px) {
      margin-bottom: 50px;
    }

    .logo {
      color: #000000;
      border-color: #000000;
    }

    .main-nav__link {
      color: #000000;

      @media (max-width: 768px) {
        color: $white;
        opacity: 0.6;
      }
    }
  }
}
