.page {
  height: 100%;
}

.page__body {
  font-family: 'Roboto', sans-serif;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 120%;

  &--opened-menu {
    overflow: hidden;
  }
}
