.section {
  position: relative;
  display: flex;
  justify-content: center;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 100%;
  background-size: cover;

  &--1 {
    background-image: url('../img/about.webp');

  }

  &--2 {
    background-image: url('../img/gallery.webp');
  }


  &--3 {
    background-image: url('../img/info.webp');
  }

  &__title {

    @media (max-width: 768px) {
      margin-top: 150%;
    }
  }

  &__link {
    height: min-content;
    letter-spacing: 5px;
    font-family: 'FuturaPT', sans-serif;
    font-weight: 400;
    font-size: 62px;
    line-height: 82px;
    text-transform: uppercase;
    color: $white;
    animation: sectionTitleOff 1s ease-out;
    // animation-fill-mode: forwards;

    @media (max-width: 768px) {
      font-size: 28px;
    }


    &:hover {
      // letter-spacing: 3px;
      // opacity: 0.6;
      animation: sectionTitleOn 1s ease-out;
      animation-fill-mode: forwards;
    }
  }

  &__footer {
    // visibility: hidden;
    z-index: 0 !important;

  }
}



.pp-tableCell {
  display: flex;
  margin-top: 15%;
}

.section--last {
  @media (max-width: 768px) {
    .section__title {

      margin-top: 60%;

    }
  }
}

.loader {
  padding-top: 15%;


}

.pagepiling::after {
  position: absolute;
  bottom: 5%;
  left: 50%;
  content: '';
  width: 20px;
  height: 20px;
  background-image: url('../img/icon-arrow-light.svg');
  background-repeat: no-repeat;
  background-size: contain;
  transform: translateX(-50%) rotate(180deg);
  z-index: 5;
  animation: slideDown 4s linear infinite;

  @media (max-width: 768px) {
    display: none;
  }
}

@keyframes slideDown {
  0% {
    bottom: 10%;
  }

  10% {
    bottom: 5%;

  }

  20% {
    bottom: 10%;
  }

  30% {
    bottom: 5%;
  }

  // 40% {
  //   bottom: 10%;
  // }
  50% {
    bottom: 10%;
  }

  100% {
    bottom: 10%;
  }
}
