.page-main {

  //   display: flex;
  // flex-direction: column;
  // overflow: hidden;
  // touch-action: none;
  // position: relative;

  .page-main__title {
    margin: 0;
    margin-bottom: 25px;
    font-size: 35px;
    line-height: 120%;

    @media (max-width: 768px) {
      margin-bottom: 15px;
      font-size: 20px;
    }
  }

  .page-main__price {
    font-size: 52px;
    font-weight: 400;

    @media (max-width: 768px) {
      font-size: 38px;
      margin: 30px 0;
    }
  }

  .page-main__text {
    max-width: 450px;
    margin: 0;
    margin-bottom: 10px;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    line-height: 140%;

    @media (max-width: 768px) {
      font-size: 16px;
    }
  }

  .page-main__list {
    margin: 0;
    padding: 0;
    list-style: none;

    @media (max-width: 768px) {
      margin-bottom: 20px;
    }
  }

  .page-main__item {
    margin-bottom: 5px;
    font-family: 'Roboto', sans-serif;
    line-height: 140%;
    font-size: 14px;

    @media (max-width: 768px) {
      font-size: 16px;
    }
  }

  b {
    display: block;
    font-family: 'Roboto', sans-serif;
    line-height: 140%;
    font-size: 16px;

    @media (max-width: 768px) {
      font-size: 16px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }

  &--about {
    display: flex;
    width: 100%;
    padding-top: 50px;

    @media (max-width: 768px) {
      flex-direction: column;
      padding-top: 0;
    }

    img {
      width: 300px;
      margin-right: 50px;

      @media (max-width: 768px) {
        padding-top: 30px;
        margin: 0 auto;
        order: 1;
      }
    }

    // .page-main__title {
    //   margin: 0;
    //   margin-bottom: 25px;
    //   font-size: 35px;
    //   line-height: 120%;
    // }

    // .page-main__text {
    //   width: 450px;
    //   margin: 0;
    //   margin-bottom: 10px;
    //   font-family: 'Roboto';
    //   font-size: 14px;
    //   line-height: 140%;
    // }
  }

  &--info {
    .page-main__text {
      margin-bottom: 20px;
      font-size: 16px;

      @media (max-width: 768px) {}

      .page-main__column:first-child {
        padding-right: 20px;
      }
    }
  }

  &--price {
    display: flex;

    @media (max-width: 768px) {
      flex-direction: column;
      padding-top: 0;
    }

    .page-main__column {
      max-width: 33%;

      @media (max-width: 768px) {
        max-width: none;
      }
    }

    .page-main__title {
      @media (max-width: 1024px) {
        font-size: 28px;
      }
    }

    .page-main__text {
      margin-bottom: 60px;
      font-size: 14px;

      @media (max-width: 768px) {
        margin-bottom: 30px;
      }
    }

    b {
      margin: 0;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: 14px;

      @media (max-width: 768px) {
        margin: 0;

      }
    }
  }


  &__link-price {
    position: absolute;
    top: 50%;
    right: 5%;
    padding-right: 40px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
    color: #ffffff;
    z-index: 11;

    @media (max-width: 768px) {
      right: 50%;
      transform: translateX(50%);
      bottom: 20%;
      top: auto;
    }

    &::after {
      position: absolute;
      right: 10px;
      // top: center;
      content: '';
      width: 20px;
      height: 20px;
      transform: rotate(90deg);
      background-image: url('../img/icon-arrow-light.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: 90%;
      opacity: 0.5;
      transition: 500ms;
    }

    &:hover:after {
      right: 0;
      transition: 500ms;
      background-size: 100%;
      opacity: 1;
    }
  }

  &__column {
    flex-grow: 1;
  }

}
