.gallery {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-column-gap: 80px;
  grid-row-gap: 80px;
  margin: 0;
  padding: 0;
  list-style: none;

  @media (max-width: 768px) {
    grid-template-columns: auto auto;
    grid-column-gap: 30px;
    grid-row-gap: 40px;
  }

  li {
    max-width: 350px
  }

  &__img {
    cursor: pointer;
    display: block;
  }

  &__full-photo {
    position: fixed;
    max-width: 100%;
    max-height: 100%;
    width: auto;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 12;

    @media (max-width: 768px) {
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.background {
  position: fixed;
  background-color: #ffffff;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 11;
  cursor: pointer;
}
